@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

$primaryColor: #333333;
$secondaryColor: #ababab;

body {
  overflow-x: hidden;
  width: 100vw;
}

.container {
  min-height: 100vh;
  height: auto;
  display: flex;
  flex-flow: row nowrap;
  font-family: "Roboto", sans-serif;
  color: $primaryColor;
}

.wrapper {
  flex-basis: 960px/1440px * 100vw;
  display: flex;
  flex-flow: column nowrap;
  padding: 40px 140px/1440px * 100vw;

  @media screen and (max-width: 1199px) {
    padding: 40px 40px/834px * 100vw;
    flex-basis: 100vw;
  }

  @media screen and (max-width: 575px) {
    padding: 20px;
  }

  header {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;

    h1 {
      font-style: normal;
      font-weight: bold;
      font-size: 20px/16px * 1rem;
      line-height: 28px/16px * 1rem;
    }

    .header-favourite-btn {
      display: none;
      cursor: pointer;

      &:hover {
        text-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
      }

      @media screen and (max-width: 1199px) {
        margin-left: auto;
        display: flex;
        flex-flow: row nowrap;

        h3 {
          margin-left: 10px;
        }
      }

      h3 {
        font-style: normal;
        font-weight: 500;
        font-size: 20px/16px * 1rem;
        line-height: 28px/16px * 1rem;
        color: $secondaryColor;
      }
    }
  }

  main {
    .main-heading {
      margin-top: 78px;

      h2 {
        font-style: normal;
        font-weight: bold;
        font-size: 32px/16px * 1rem;
        line-height: 44px/16px * 1rem;
      }

      h3 {
        font-style: normal;
        font-weight: 500;
        font-size: 24px/16px * 1rem;
        line-height: 44px/16px * 1rem;
      }
    }

    form {
      margin-top: 43px;
      display: flex;
      flex-flow: column nowrap;

      div {
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        margin-bottom: 10px;

        &:last-of-type {
          margin-bottom: 20px;
        }

        label {
          font-style: normal;
          font-weight: normal;
          font-size: 18px/16px * 1rem;
          line-height: 26px/16px * 1rem;
        }

        input[type="radio"] {
          margin-right: 15px;
          width: 20px;
          height: 20px;
        }
      }

      .categories-btns {
        flex-flow: row wrap;
        padding: 10px 10px 5px;

        .category-btn {
          padding: 4px 13px;
          margin-right: 10px;
          margin-bottom: 5px;
          border: 2px solid #f8f8f8;
          border-radius: 6px;
          background: none;
          font-style: normal;
          font-weight: 500;
          font-size: 12px/16px * 1rem;
          line-height: 16px/16px * 1rem;
          letter-spacing: 2px/16px * 1rem;
          text-transform: uppercase;
          color: $secondaryColor;
        }

        input {
          display: none;

          &:checked + label {
            background: #f8f8f8;
            color: $primaryColor;
          }
        }
      }

      input[type="text"] {
        display: none;
        padding: 8px 13px;
        margin-bottom: 20px;
        outline: none;
        font-style: normal;
        font-weight: normal;
        font-size: 16px/16px * 1rem;
        line-height: 22px/16px * 1rem;
        border: 2px solid $primaryColor;
        border-radius: 10px;
      }

      button[type="submit"] {
        transition: all 0.1s ease-out 0.1s;
        font-style: normal;
        font-weight: bold;
        font-size: 16px/16px * 1rem;
        line-height: 22px/16px * 1rem;
        color: #ffffff;
        background: linear-gradient(90.55deg, #8ea7ff 0%, #7291ff 100%);
        border: none;
        border-radius: 10px;
        width: 152px;
        height: 42px;

        &:hover {
          cursor: pointer;
          transform: scale(1.05);
        }
      }
    }

    .main-jokecards-wrapper {
      margin-top: 40px;
      display: flex;
      flex-flow: column nowrap;

      .main-jokecard {
        padding: 40px;
        background: #f8f8f8;
        border-radius: 20px;
        margin-bottom: 20px;
        display: flex;
        flex-flow: column nowrap;

        @media screen and (max-width: 575px) {
          padding: 20px;
        }

        .main-jokecard-like {
          margin-left: auto;
          &:hover {
            cursor: pointer;
          }
        }

        .main-jokecard-body {
          display: flex;
          flex-flow: row nowrap;
          margin-top: 10px;

          .main-jokecard-body-icon {
            margin-right: 20px;
          }

          .main-jokecard-body-content {
            flex: 1;
            display: flex;
            flex-flow: column nowrap;

            .main-jokecard-body-content-id {
              margin-bottom: 5px;
              font-style: normal;
              font-weight: 500;
              font-size: 10px/16px * 1rem;
              line-height: 14px/16px * 1rem;
              color: $secondaryColor;

              a {
                color: #8ea7ff;
              }
            }

            .main-jokecard-body-content-text {
              font-style: normal;
              font-weight: normal;
              font-size: 18px/16px * 1rem;
              line-height: 26px/16px * 1rem;
              margin-bottom: 23px;

              @media screen and (max-width: 575px) {
                margin-bottom: 28px;
              }
            }

            .main-jokecard-body-content-info {
              display: flex;
              flex-flow: row nowrap;
              align-items: center;

              @media screen and (max-width: 575px) {
                flex-flow: column nowrap;
                align-items: flex-start;
              }

              .main-jokecard-body-content-info-update {
                font-style: normal;
                font-weight: normal;
                font-size: 10px/16px * 1rem;
                line-height: 14px/16px * 1rem;
                color: $secondaryColor;
              }

              .main-jokecard-body-content-info-category {
                margin-left: auto;
                font-style: normal;
                font-weight: 500;
                font-size: 10px/16px * 1rem;
                line-height: 14px/16px * 1rem;
                letter-spacing: 2px/16px * 1rem;
                text-transform: uppercase;
                background: #ffffff;
                border-radius: 6px;
                padding: 6px 20px;

                @media screen and (max-width: 575px) {
                  margin-top: 10px;
                  margin-left: 0;
                }
              }
            }
          }
        }
      }
    }
  }
}

.show {
  display: flex !important;
}

.aside-wrapper {
  flex-basis: 480px/1440px * 100vw;

  @media screen and (max-width: 1199px) {
    display: none;
    position: fixed;
    width: 100vw;
    height: 100%;
    top: 0;
    background: rgba(0, 0, 0, 0.3);
  }

  aside.favourite {
    flex-grow: 1;
    height: 100%;
    background: #f8f8f8;
    padding: 40px;

    @media screen and (max-width: 1199px) {
      overflow-y: scroll;
      flex-grow: 0;
      flex-basis: 480px;
      height: auto;
      margin-left: auto;
    }

    @media screen and (max-width: 575px) {
      flex-grow: 0;
      padding: 20px;
      flex-basis: 100vw;
      margin-left: 0;
    }

    .favourite-btn {
      display: flex;
      flex-flow: row nowrap;
      margin-bottom: 20px;

      @media screen and (max-width: 1199px) {
        cursor: pointer;
        justify-content: flex-end;
      }

      svg {
        display: none;
        margin-right: 10px;

        @media screen and (max-width: 1199px) {
          display: block;
        }
      }

      h3 {
        font-style: normal;
        font-weight: 500;
        font-size: 20px/16px * 1rem;
        line-height: 28px/16px * 1rem;
        color: $secondaryColor;
      }
    }

    .aside-jokecard {
      padding: 19px;
      background: #ffffff;
      border: 1px solid #f8f8f8;
      box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      margin-bottom: 20px;
      display: flex;
      flex-flow: column nowrap;

      .aside-jokecard-like {
        margin-left: auto;
        &:hover {
          cursor: pointer;
        }
      }

      .aside-jokecard-body {
        display: flex;
        flex-flow: row nowrap;
        margin-top: 10px;

        .aside-jokecard-body-icon {
          margin-right: 20px;
        }

        .aside-jokecard-body-content {
          display: flex;
          flex-flow: column nowrap;

          .aside-jokecard-body-content-id {
            margin-bottom: 5px;
            font-style: normal;
            font-weight: 500;
            font-size: 10px/16px * 1rem;
            line-height: 14px/16px * 1rem;
            color: $secondaryColor;

            a {
              color: #8ea7ff;
            }
          }

          .aside-jokecard-body-content-text {
            font-style: normal;
            font-weight: normal;
            font-size: 14px/16px * 1rem;
            line-height: 20px/16px * 1rem;
            margin-bottom: 20px;
          }

          .aside-jokecard-body-content-info {
            .aside-jokecard-body-content-info-update {
              font-style: normal;
              font-weight: normal;
              font-size: 10px/16px * 1rem;
              line-height: 14px/16px * 1rem;
              color: $secondaryColor;
            }
          }
        }
      }
    }
  }
}
